.settings {
  position: fixed;
  width: 30vw;
  height: 70vh;
  background-color: whitesmoke;
  border-radius: 5vh;
  z-index: 10002;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

  animation: appear 0.25s;

  &__title {
    text-align: center;
    color: black;
    font-size: 3.5vh;
    margin-top: 4vh;
  }

  &__form {
    width: 30vw;
    height: 55vh;

    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;

    &__button {
      width: 20vw;
      height: 5vh;
      background-color: #333;
      border: none;
      font-size: 2vh;
      color: white;
      cursor: pointer;
      border-bottom: solid #555 0.5vh;

      transition: background-color 250ms;
    }

    &__button:hover {
      background-color: #444;
    }

    &__button:focus {
      border-bottom: solid #555 0.15vh;
    }

    &__element {
      width: 25vw;
      height: 6vh;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &__label {
        color: black;
      }

      &__input {
        height: 3vh;
      }
    }
  }

  @media screen and (orientation: portrait) {
    width: 85vw;

    &__title {
      font-size: 4.5vh;
    }

    &__form {
        width : 85vw;

        &__button {
            width : 40vw;
        }

        &__element {
            width : 70vw;
            height : 6vh;
        }
    }
  }
}

@keyframes appear {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
