.Timer {
    color : white;
    font-size : 16vh;
    font-weight: 500;
    width : 100%;
    max-width: 100%;
    text-align: center;

    text-shadow: 1vh 1vh 1vh rgb(19, 18, 18);

    @media screen and (orientation : portrait){
        font-size: 12vh;
        width : 100vw;
        max-width: 100vw;
    }

    @media screen and (width : 1280px) and (  height : 800px ){
        font-size : 10vh;
    }
}